// content section 1
.masco--content-section {
    background-color: var(--warning-900);
    @include max(1199px) {
        text-align: center;
    }
    @include max(767px) {
        padding: 20px 0;
    }
    .masco--content-section-video {
        position: relative;
        @include max(1199px) {
            margin: 20px 0;
        }
        .masco--content-play-btn {
            position: absolute;
            top: 50%;
            left: 46%;
            transform: translate(-50%,-50%);
            background-color: var(--warning-700);
            width: 100px;
            height: 100px;
            text-align: center;
            line-height: 100px;
            border-radius: 50%;
            @include max(1399px) {
                left: 50%;
            }
        }
    }
    .masco--content-section-text {
        p{
            padding-bottom: 50px;
            border-bottom: 1px solid rgba(10, 16, 47, 0.1);
            margin-bottom: 50px;
            @include max(1399px) {
                margin-bottom: 20px;
                padding-bottom: 20px;
            }
        }
        span{
            font-style: italic;
            font-weight: 600;
        }
        .masco--single-content {
            display: flex;
            margin-top: 24px;
            @include max(1399px) {
                margin-top: 12px;
            }
            @include max(1199px) {
                display: block;
            }
            .masco--single-content-text {
                margin-left: 15px;
                span{
                    font-style: normal;
                    margin-bottom: 5px;
                    font-weight: 600;
                    color: var(--gray-900);
                }
                p{
                    font-size: 14px;
                    line-height: 20px;
                    color: var(--gray-900);
                    opacity: 0.7;
                    border-bottom: none;
                    margin-top: 5px;
                    padding-bottom: 0;
                    margin-bottom: 0;
                }
            }
        }
    }
}


