// logo area
.masco--logo-area{
    .masco--brandlogo-text{
        margin-top: 100px;
        margin-bottom: 80px;
        @include max(991px) {
            margin-bottom: 50px;
            margin-top: 20px;
        }
        p{
            text-align: center;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            opacity: 0.7;
        }
    }
    .masco--brand-logo{
        padding-bottom: 100px;
        border-bottom: 1px solid rgba(10, 16, 47, 0.1);
        @include max(991px) {
            padding-bottom: 50px;
        }
        img{
            margin: 0 20px;
        }
    }
}


// logo section
.masco--logo-section {
    .masco--logo-img {
        margin-top: 120px;
    }
    .masco--logo-image {
        margin-top: 162px;
    }
    .masco--logo-area {
        .masco--brand-logo {
            border-bottom: none;
        }
    }
}