// cta 1
.masco--cta-area {
    padding-top: 120px;
    position: relative;
    background-color: var(--gray-900);
    .masco-cta1 {
        border-bottom: 1px solid rgba(255,255,255,0.1);
        padding-bottom: 80px;
        margin-bottom: 0;
    }
    .cta-title {
        color: var(--warning-700);
    }
    .masco--cta-btn {
        &:hover {
            color: var(--gray-900);
            background-color: var(--warning-700);
            transition: all 0.4s ease;
        }
    }
    .masco--cta-img {
        position: absolute;
        top: 9%;
        right: 9%;
        @include max(1399px) {
            right: 1%;
        }
        @include max(767px) {
            display: none;
        }
    }
}



// cta 2
.masco--cta2-section {
    background-color: var(--pink-400);
    position: relative;
    .cta2-btn{
        text-align: center;
        transition: all 0.4s ease;
        a{
            &:hover {
                color: var(--gray-900);
                background-color: var(--warning-700);
            }
        }
    }
    .masco--cta2-left-img {
        position: absolute;
        top: 12%;
        left: 5%;
        @include max(991px) {
            display: none;
        }
    }
    .masco--cta2-right-img {
        position: absolute;
        bottom: 0;
        right: 3%;
        @include max(767px) {
            display: none;
        }
    }
}


// cta 3
.masco--cta3-section {
    padding-top: 180px;
    @include max(991px) {
        text-align: center;
    }
    .yellow-cta {
        background-color: var(--warning-550);
        border-radius: 50px;
        align-items: center;
    }
    .masco--cta3-image {
        position: relative;
    }
    .masco--cta3-mobile-img {
        position: absolute;
        top: -11.5%;
        left: 14%;
        @include max(1199px) {
            top: -31%;
        }
        @include max(991px) {
            top: -11.5%;
            left: 25%;
        }
        @include max(767px) {
            top: -15%;
            left: 18%;
        }
        @include max(575px) {
            top: -11%;
        }
    }
    .masco--cta3-content {
        margin-right: 65px;
        p{
            margin-top: 20px;
            margin-bottom: 50px;
        }
    }
}