// reset password
.masco--reset-password-page {
    background-color: var(--warning-900);
    .brand-logo {
        text-align: center;
        margin-bottom: 100px;
        max-width: 100%;
    }
    .masco--reset-password-title{
        text-align: center;
    }
    .masco--reset-password-form {
        background-color: var(--warning-700);
        border: 2px solid var(--gray-900);
        border-radius: 10px;
        padding: 40px;
        margin-top: 60px;
        p{
            font-weight: 600;
            color: var(--gray-900);
            margin-bottom: 10px;
        }
        input[type="email"] {
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.5;
            width: 100%;
            border: 1px solid rgba(10, 16, 47, 0.5) !important;
            border-radius: 50px;
        }
        .masco--reset-password-btn {
            width: 100%;
            margin-top: 24px;
            margin-bottom: 15px;
            a{
                color: var(--warning-700);
            }
        }
        .remember-password {
            text-align: center;
            p{
                font-family: $body-font;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: var(--gray-900);
                opacity: 0.8;
            }
            a{
                color: var(--primary-700);
            }
        }
    }
}

// 404 page
.masco--404-page {
    .page-not-found {
        text-align: center;
        .page-not-found-img {
            margin-top: 80px;
            margin-bottom: 30px;
            img{
                object-fit: cover;
                object-position: center center;
            }
        }
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--gray-900);
            opacity: 0.8;
            max-width: 350px;
            margin: 0 auto 40px;
        }
        .page-not-found-btn {
            a{
                color: var(--warning-700);
            }
        }
    }
}