// pricing 1
.masco--pricing-section {
    background-color: rgba(162, 89, 255, 0.03);
    .active {
        box-shadow: 8px 8px var(--gray-900);
    }
    .masco--pricing-box {
        background-color: var(--warning-700);
        border: 2px solid var(--gray-900);
        border-radius: 10px;
        padding: 40px;
        @include max(991px) {
            margin-bottom: 50px;
        }
        &:hover{
            box-shadow: 8px 8px var(--gray-900);
            transition: all 0.4s ease;
        }
        .masco--pricing-name {
            text-align: center;
            margin-bottom: 20px;
        }
        .masco--price-set {
            display: flex;
            justify-content: center;
            h6{
                font-family: $body-font;
                font-weight: 400;
                font-size: 28px;
                line-height: 80px;
                color: var(--gray-900);
            }
        }
        .masco--pricing-description {
            padding-bottom: 40px;
            border-bottom: 1px solid rgba(10,16,47,0.1);
            p{
                font-family: $body-font;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: var(--gray-900);
                opacity: 0.7;
                text-align: center;
            }
        }
        .masco--pricing-feature {
            padding-top: 40px;
            padding-left: 20px;
            margin-bottom: 50px;
            li{
                font-weight: 600;
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 24px;
                opacity: 1;
            }
        }
        .masco--pricing-btn {
            .pricing-btn {
                width: 100%;
            }
        }
    }
}
