.masco-social-icon {
    @include max(991px) {
        margin-bottom: 20px;
    }
    @include max(575px) {
        margin-bottom: 0;
    }
    ul{
        display: flex;
        li{
            width: 30px;
            height: 30px;
            margin-right: 15px;
            a{
                display: block;
                background-color: rgba(255, 255, 255, 0.1);
                text-align: center;
                border-radius: 50%;
                &:hover{
                    background-color: var(--primary-700);
                    transition: all 0.3s ease;
                }
            }
        }
    }
}



// footer 1
.masco-footer-section{
    padding-top: 120px;
    padding-bottom: 85px;
    background-color: var(--gray-900);
    @include max(767px) {
        padding-top: 80px;
        padding-bottom: 50px;
    }
    .masco-textarea {
        p{
            margin-top: 30px;
            font-family: $body-font;
            font-weight: 400;
            font-size: 18px;
            line-height: 30px;
            color: var(--warning-700);
            opacity: 0.8;
            margin-bottom: 18px;
            @include max(1399px) {
                margin: 10px 0;
            }
        }
        .footer-mail {
            margin-bottom: 12px;
            margin-top: 0;
            @include max(1399px) {
                margin-bottom: 10px;
            }
            a{
                color: var(--warning-700);
                opacity: 0.8;
            }
        }
    }
    .masco-footer-menu {
        margin-left: 30px;
        @include max(1399px) {
            margin-left: 0;
        }
        @include max(1199px) {
            margin-top: 50px;
        }
        @include max(575px) {
            margin-bottom: 0;
        }
        span{
            color: var(--warning-700);
            margin-bottom: 30px;
            font-family: $body-font;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
        }
        ul{
            margin-top: 20px;
            @include max(575px) {
                margin-top: 15px;
            }
            li{
                margin-top: 10px;
                a{
                    color: var(--warning-700);
                    opacity: 0.8;
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                    &:hover {
                        color: var(--primary-700);
                    }
                }
            }
        }
    }
    .left-menu {
        margin-left: 100px;
        @include max(1199px) {
            margin-left: 0;
        }
    }
}
.masco--copyright-section {
    background-color: #161C39;
    .masco--copyright-area {
        padding: 18px 0;
        text-align: center;
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: var(--warning-700);
            opacity: 0.8;
            a{
                color: var(--warning-700);
            }
        }
    }
}


// footer 2
.footer-two {
    background-color: var(--warning-700);
    .masco-textarea {
        margin-right: 75px;
        p{
            color: var(--gray-900);
            margin-bottom: 43px;
        }
    }
    .masco-social-icon {
        ul{
            li{
                a{
                    background-color: rgba(10,16,47,0.1);
                    path {
                        fill: var(--gray-900);
                    }
                    &:hover {
                        path {
                            fill: var(--warning-700);
                        }
                        background-color: var(--gray-900);
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }
    .masco-footer-menu {
        span{
            color: var(--gray-900);
        }
        ul{
            li{
                a{
                    color: var(--gray-900);
                }
            }
        }
    }
    .masco-footer-download {
        margin-top: 30px;
        a{
            display: block;
            margin-top: 13px;
        }
    }
}
.white-copyright {
    background-color: var(--warning-700);
    .masco--copyright-area {
        padding: 18px 0;
        text-align: center;
        border-top: 1px solid rgba(10,16,47,0.1);
        p{
            font-family: $body-font;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: var(--gray-900);
            opacity: 0.8;
            a{
                color: var(--gray-900);
            }
        }
    }
}



// footer 3
.footer-three{
    background-color: var(--warning-700);
    .masco-textarea{
        p{
            color: var(--gray-900);
        }
    }
    .masco-social-icon {
        @include max(991px) {
            margin-bottom: 20px;
        }
        @include max(575px) {
            margin-bottom: 40px;
        }
        ul{
            li{
                a{
                    path {
                        fill: var(--gray-900);
                    }
                    background-color: rgba(10,16,47,0.1);
                    &:hover{
                        background-color: var(--warning-550);
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }
    .masco-footer-menu {
        span{
            color: var(--gray-900);
        }
        ul{
            li{
                a{
                    color: var(--gray-900);
                }
            }
        }
    }
}
.white-copyright2 {
    background-color: var(--warning-900);
    .masco--copyright-area {
        padding: 18px 0;
        text-align: center;
        p{
            font-size: 16px;
            line-height: 24px;
            color: var(--gray-900);
            opacity: 0.8;
            a{
                color: var(--gray-900);
                opacity: 0.8;
            }
        }
    }
}