.masco--integration-section {
    @include max(991px) {
        text-align: center;
    }
    .masco--integration-content {
        @include max(991px) {
            margin: 20px 0;
        }
    }
    .masco--integration-image {
        text-align: right;
        img{
            margin: 0 30px 30px 0;
        }
        @include max(991px) {
            text-align: center;
        }
    }
}