
.masco--login-btn {
    font-family: $body-font;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray-900);
     padding: 13px 30px;;
    &:hover {
        color: var(--primary-700);
    }
}

.nav-link-item {
    &:hover {
        color: var(--primary-700);
    }
}
.sub-menu--item {
    &:hover {
        color: var(--primary-700);
    }
}

header.site-header{
    position: fixed;
    z-index: 9;
    width: 100%;
    top: 0;
    padding: 15px 0;
    transition: all 0.4s;
}

header.site-header.sticky-menu{
    padding: 13px 0;
    background-color: #fff;
    box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
}

.masco-responsive-btn{
    display: none;
}