// faq 1
.masco--faq-area {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include max(767px) {
        padding-bottom: 20px;
    }
    .accordion {
        max-width: 1076px;
        margin: 0 auto;
        .accordion-item {
            border: 1px solid var(--gray-900);
            border-bottom: none;
            &:last-child{
                border-bottom: 1px solid var(--gray-900);
            }
            .accordion-header{
                padding: 15px;
                font-family: $body-font;
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: var(--gray-900);
            }
            .accordion-body {
                padding: 1rem 2.25rem;
                font-family: $body-font;
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
                opacity: 0.8;
            }
        }
    }
    .accordion-button{
        background-color: var(--warning-700);
        color: var(--gray-900);
        font-family: $body-font;
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        box-shadow: none;
    }
}
.masco--faq-btn{
    text-align: center;
    margin-top: 80px;
}

.accordion-button {
    &:after{
        content: '';
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        position: absolute;
        background-size: 24px;
        right: 0;
        background-image: url('');
    }
}
.accordion-button:not(.collapsed) {
    &:after{
        background-image: url('');
    }
}


// faq 2
.masco--faq2-section {
    .masco--faq-description {
        padding-bottom: 40px;
        margin-bottom: 40px;
        margin-right: 30px;
        border-bottom: 1px solid rgba(10,16,47,0.1);
        &:last-child {
            padding-bottom: 0;
            margin-bottom: 0;
            border-bottom: none;
        }
    }
}


// faq 3
.masco--faq3-section {
    .faq-title{
        margin-bottom: 40px;
    }
    .accordion{
        max-width: 846px;
        margin: 0 auto;
        margin-bottom: 40px;
        .accordion-item {
            border: 2px solid var(--gray-900);
            border-radius: 10px;
            margin-bottom: 24px;
        }
    }
    .accordion-button{
        background-color: var(--warning-700);
        color: var(--gray-900);
        font-family: $body-font;
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        box-shadow: none;
        &:not(.collapsed) .accordion-item {
            box-shadow: 5px 5px var(--gray-900);
        }
    }
}