// service 1
.section{
    position: relative;
    .masco--service-bg-img {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: -1;
    }
}

// service 2
.service-area2 {
    border-bottom: 1px solid rgba(10, 16, 47, 0.1);
    .single-service {
        border: none;
        &:hover {
            background-color: var(--warning-700);
            box-shadow: 0px 4px 60px rgba(10, 16, 47, 0.06);
        }
    }
}



// single service
.masco--single-service {
    .service-details{
        max-width: 1076px;
        margin: 0 auto;
        .masco-single-service-img {
            margin-bottom: 30px;
            img{
                object-fit: cover;
                object-position: center top;
            }
        }
        .masco--single-service-content {
            .masco--icon-list5 {
                ul{
                    li{
                        img{
                            @include max(1399px) {
                                left: -4%;
                            }
                            @include max(1199px) {
                                left: -5%;
                            }
                            @include max(991px) {
                                top: 3%;
                                left: -8%;
                            }
                            @include max(767px) {
                                top: 1%;
                                left: -13%;
                            }
                        }
                    }
                }
            }
            h4{
                letter-spacing: -0.5px;
            }
        }
        .masco-single-service-thumbnail {
            margin-bottom: 30px;
            margin-top: 9px;
            img{
                object-fit: cover;
                object-position: center center;
            }
        }
    }
}
