.masco--section-title {
  max-width: 636px;
  margin: 0 auto 80px;
  text-align: center;
  p{
    margin-top: 20px;
    margin-bottom: 80px;
    @include max(1399px) {
        margin-bottom: 50px;
    }
    @include max(1199px) {
        margin-bottom: 30px;
    }
}
}


// 2column section title
.masco--2column-title {
  margin-bottom: 60px;
  @include max(991px) {
    margin-bottom: 40px;
    text-align: center;
  }
  @include max(767px) {
    margin-bottom: 30px;
  }
  h2{
    @include max(991px) {
      margin-bottom: 25px;
    }
  }
}



