// content box 1
.masco--content-box {
    background-color: var(--warning-900);
    @include max(991px) {
        text-align: center;
    }
    .masco--first-content-image {
        position: relative;
        text-align: right;
        img{
            object-fit: cover;
            object-position: center top;
        }
        @include max(991px) {
            text-align: center;
        }
        .masco--first-content-shape {
            position: absolute;
            top: -10%;
            right: -10%;
            @include max(1399px) {
                right: -11%;
            }
            @include max(1199px) {
                right: -14%;
                top: -14%;
            }
            @include max(991px) {
                right: 4%;
                top: -10%;
            }
            @include max(767px) {
                right: -12%;
            }
            @include max(575px) {
                display: none;
            }
        }
    }

    .masco--2nd-content-image {
        position: relative;
        .masco--2nd-content-shape {
            position: absolute;
            bottom: -2%;
            left: -7%;
            @include max(1199px) {
                left: -7%;
            }
            @include max(991px) {
                left: 8%;
            }
            @include max(767px) {
                left: -6%;
            }
            @include max(575px) {
                display: none;
            }
        }
    }
}

.masco--default-content{
    @include max(991px) {
      text-align: center;
    }
  }


// content box 2
.masco--content-box2 {
    .masco--2nd-content-image {
        text-align: right;
        @include max(991px) {
            text-align: center;
        }
    }
    .content-text{
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(10, 16, 47, 0.1);
    }
}


// content box 3
.content-box3{
    .masco--default-content {
        margin-right: 30px;
    }
    .masco--content-img {
        @include max(1199px) {
            text-align: center;
        }
    }
    .masco--content-image {
        text-align: right;
        @include max(1199px) {
            text-align: center;
            margin: 50px;
        }
    }
}