// video-section
.masco--video-section {
    position: relative;
    .masco--single-video-section {
        @include max(991px) {
            text-align: center;
        }
        .masco--video-image {
            position: relative;
            img{
                object-fit: cover;
                object-position: center top;
            }
            .masco-video-play-btn {
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%,-50%);
            }
        }
        h4{
            margin-top: 24px;
        }
        p{
            margin-right: 45px;
            @include max(991px) {
                margin: 0 130px 20px;
            }
            @include max(767px) {
                margin: 0 50px 50px;
            }
        }
    }
    .video-btn {
        margin-top: 80px;
        text-align: center;
        @include max(991px) {
            margin: 20px;
        }
    }
    .masco--video-shape {
        position: absolute;
        top: 18%;
        @include max(575px) {
            opacity: 0;
        }
    }
}