// career
.masco--career-area {
    padding-bottom: 0;
    .masco--career-img {
        margin-bottom: 100px;
    }
    .iconbox {
        display: block;
        .job-meta {
            margin-bottom: 30px;
        }
    }
    .masco--career-btn {
        margin-top: 80px;
        text-align: center;
    }
}


// career details
.masco--career-details-page {
    .masco--career-details-content {
        h2{
            font-size: 48px;
            margin-bottom: 18px;
        }
        p{
            margin-right: 100px;
        }
        .job-meta{
            p{
                margin: 0 27px 0 7px;
            }
        }
        .masco--job-details {
            h4{
                margin-bottom: 18px;
                line-height: 30px;
            }
            ul{
                li{
                    font-family: $body-font;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                    color: var(--gray-900);
                    opacity: 0.8;
                    list-style: inside !important;
                    margin-bottom: 18px;
                }
            }
        }
        .masco--career-info {
            display: flex;
            align-items: center;
            margin-top: 30px;
            .masco--career-info-content {
                margin-left: 20px;
                span{
                    font-family: $body-font;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 28px;
                    color: var(--gray-900);
                }
            }
        }
    }
}

.job-meta{
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    p{
        color: var(--gray-900);
        margin: 0 27px 0 7px;
    }
}

.career-details-contact {
    h4{
        margin-bottom: 40px;
    }
    textarea{
        margin-bottom: 24px;
    }
    .checkbox {
        margin-bottom: 40px;
        display: flex;
        align-items: center;
        input[type="checkbox"] {
            width: 19px;
            height: 19px;
            border: 1px solid var(--gray-900) !important;
            border-radius: 5px;
        }
        label{
            font-family: $body-font;
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            color: var(--gray-900);
            margin-left: 9px;
        }
    }
}