// main blog
.masco--blog-section {
    background-color: var(--warning-700);
    .masco--single-blog {
        background: #FFFFFF;
        box-shadow: 0px 4px 60px rgba(10, 16, 47, 0.08);
        border-radius: 10px;
        margin-bottom: 40px;
        .masco--single-blog-img {
            img{
                object-fit: cover;
                object-position: center top;
                width: 100%;
            }
        }
        .masco--single-blog-content {
            padding: 30px;
            .masco--single-blog-text{
                display: flex;
                .masco--blog-author {
                    img{
                        margin-right: 10px;
                    }
                    a{
                        font-family: $body-font;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 24px;
                        color: var(--gray-900);
                        opacity: 0.8;
                        margin-right: 24px;
                        margin-bottom: 0;
                    }
                }
                .masco--blog-date {
                    a{
                        font-family: $body-font;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 24px;
                        color: var(--gray-900);
                        opacity: 0.8;
                        margin-right: 24px;
                        margin-bottom: 0;
                    }
                }
                .masco--blog-catagory {
                    background: rgba(10, 16, 47, 0.05);
                    border-radius: 50px;
                    padding: 6px 26px;
                    a{
                        color: rgba(10, 16, 47, 0.6);
                        text-align: center;
                        margin: 0;
                    }
                }
            }
            h3{
                letter-spacing: -1px;
                margin-top: 30px;
            }
            a{
                font-family: $body-font;
                font-weight: 700;
                font-size: 16px;
                line-height: 24px;
                color: var(--gray-900);
                margin-right: 10px;
                &:hover{
                    color: var(--primary-700);
                }
            }
        }
    }
    .sidebar {
        .masco--blog-search-bar {
            background-color: var(--warning-900);
            padding: 20px;
            border-radius: 10px;
            position: relative;
            margin-bottom: 30px;
            input[type="search"] {
                background-color: var(--warning-700) !important;
                border: 1px solid var(--warning-850) !important;
                border-radius: 50px;
                font-family: $body-font;
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
                padding-left: 62px !important;
            }
            .serach-bar-icon {
                position: absolute;
                top: 36%;
                left: 10%;
                @include max(1399px) {
                    top: 35%;
                    left: 12%;
                }
                @include max(1199px) {
                    left: 5%;
                }
                @include max(991px) {
                    left: 7%;
                }
                @include max(991px) {
                    left: 7%;
                }
                @include max(767px) {
                    left: 9%;
                }
            }
        }
        .widget {
            background-color: var(--warning-900);
            border-radius: 10px;
            padding: 30px;
            margin-bottom: 30px;
            span{
                font-family: $body-font;
                font-weight: 600;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
                padding-bottom: 8px;
                border-bottom: 2px solid var(--gray-900);
            }
            .masco--blog-catagories {
                margin-top: 30px;
                ul{
                    li{
                        padding-bottom: 14px;
                        margin-bottom: 14px;
                        border-bottom: 1px solid rgba(10, 16, 47, 0.1);
                        &:nth-last-child(1) {
                            border-bottom: none;
                            margin: 0;
                            padding: 0;
                        }
                        a{
                            font-family: $body-font;
                            font-weight: 400;
                            font-size: 18px;
                            line-height: 30px;
                            color: var(--gray-900);
                            display: block;
                        }
                    }
                }
            }
            .masco--blog-recent-posts {
                margin-top: 30px;
                display: flex;
                align-items: center;
                .masco--recent-post-img {
                    margin-right: 16px;
                    img{
                        object-fit: cover;
                        object-position: center center;
                    }
                }
                .masco--recent-post-content {
                    margin-right: 12px;
                    .masco--blog-date {
                        a{
                            font-family: $body-font;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            color: var(--gray-900);
                        }
                    }
                    p{
                        color: var(--gray-900);
                        margin: 0;
                    }
                }
            }
            .blog-tag-catagory{
                margin-top: 30px;
                display: flex;
                flex-wrap: wrap;
                li{
                    a{
                        color: rgba(10, 16, 47, 0.6);
                        text-align: center;
                        margin: 0;
                        background: rgba(10, 16, 47, 0.05);
                        border-radius: 50px;
                        padding: 5px 20px;
                        margin-right: 5px;
                        display: block;
                        margin-bottom: 10px;
                    }
                }
            }
            p{
                margin-top: 30px;
                margin-bottom: 15px;
                color: var(--gray-900);
            }
            input[type="email"] {
                font-family: $body-font;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: var(--gray-900);
                opacity: 0.8;
                border: 1px solid var(--warning-850) !important;
                border-radius: 50px;
                margin-bottom: 15px;
            }
            .masco--blog-subscribe-btn {
                width: 100%;
            }
        }
    }
}



// blog details
.masco--breadcrumb-title {
    .blog-details {
        font-size: 50px;
        line-height: 60px;
        margin: 0 100px;
    }
}
.blog-details {
    .masco--single-blog {
        box-shadow: none;
        .masco--single-blog-content {
            .masco--single-blog-text {
                margin-bottom: 30px;
            }
            span{
                font-family: $body-font;
                font-weight: 600;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
            }
            .blog-details-img {
                margin-bottom: 30px;
            }
            .masco--comment-section {
                h3{
                    margin: 40px 0;
                }
                .masco--comment-wrap {
                    .masco--comment-item {
                        display: flex;
                        padding-bottom: 24px;
                        margin-bottom: 24px;
                        border-bottom: 1px solid rgba(10,16,47,0.1);
                        &:nth-child(2) {
                            padding-left: 110px;
                        }
                        .masco--comment-author {
                            flex-shrink: 0;
                            margin-right: 20px;
                            img{
                                object-fit: cover;
                                object-position: center top;
                            }
                        }
                        .masco--comment-author-data {
                            h6{
                                font-family: $body-font;
                                font-weight: 600;
                                font-size: 14px;
                                line-height: 20px;
                                color: var(--gray-900);
                            }
                            span{
                                font-size: 14px;
                                line-height: 20px;
                                color: rgba(10,16,47,0.8);
                            }
                        }
                        .comment-reply {
                            display: contents;
                        }
                    }
                }
            }
            .masco--comment-box {
                margin-bottom: 10px;
                h3{
                    margin: 40px 0;
                }
                .masco--comment-author {
                    display: flex;
                    .author-img {
                        flex-shrink: 0;
                        margin-right: 10px;
                        img{
                            object-fit: cover;
                            object-position: center top;
                        }
                    }
                    textarea{
                        border: 1px solid var(--gray-900) !important;
                        border-radius: 10px;
                    }
                }
            }
        }
    }
}