// contact contact 1
.masco--contact-form {
    background-color: var(--warning-900);
    border: 1px solid rgba(10, 16, 47, 0.5);
    border-radius: 10px;
    padding: 40px;
    form{
        p{
            font-weight: 600;
            margin-bottom: 10px;
        }
        input[type="text"],input[type="mail"] {
            background-color: var(--warning-700);
            border: 1px solid rgba(10, 16, 47, 0.5) !important;
            border-radius: 50px;
            margin-bottom: 24px;
        }
        textarea {
            background-color: var(--warning-700);
            border: 1px solid rgba(10, 16, 47, 0.5) !important;
            border-radius: 30px;
            margin-bottom: 40px;
        }
    }
}

// contact 2
.masco--full-contact-section {
    .full-contact-form {
        padding: 40px;
        p{
            font-family: $body-font;
            font-weight: 600;
            color: var(--gray-900);
            margin-bottom: 10px;
        }
        input[type="text"],input[type="email"]{
            color: var(--gray-900);
            opacity: 0.5;
            background-color: var(--warning-900) !important;
            border: 1px solid rgba(10, 16, 47, 0.5) !important;
            border-radius: 50px;
            margin-bottom: 24px;
        }
        textarea {
            color: var(--gray-900);
            opacity: 0.5;
            background-color: var(--warning-900) !important;
            border: 1px solid rgba(10, 16, 47, 0.5) !important;
            border-radius: 20px;
            margin-bottom: 24px;
        }
    }
}


// contact 3
.masco--contact-section {
    @include max(767px) {
        margin: 20px 0;
    }
    .masco--single-contact-content {
        display: flex;
        margin-bottom: 60px;
        margin-right: 185px;
        @include max(1399px) {
            margin-right: 100px;
        }
        @include max(1199px) {
            margin-right: 70px;
        }
        .masco--contact-img {
            margin-right: 30px;
            flex-shrink: 0;
        }
        .masco--single-contact-text {
            h4{
                letter-spacing: -0.5px;
            }
        }
    }
    .masco--contact-form {
        background-color: var(--warning-900);
        h4{
            letter-spacing: -0.5px;
        }
        input[type="text"],input[type="mail"],textarea {
            background-color: var(--warning-900);
            color: var(--gray-900);
            opacity: 0.5;
            border: none !important;
            border-radius: 0;
            border-bottom: 1px dashed rgba(10, 16, 47, 0.1) !important;
            margin-bottom: 40px;
            padding-bottom: 15px !important;
            padding-left: 0 !important;
        }
    }
}
.masco--contact-form-btn {
    width: 100%;
}


// contact form section
.masco--contact-form-section {
    margin-bottom: 120px;
    @include max(991px) {
        text-align: center;
    }
    .masco--contact-form-content {
        margin-right: 100px;
        @include max(1399px) {
            margin-right: 80px;
        }
        @include max(1199px) {
            margin-right: 30px;
        }
        .masco--single-content {
            @include max(1199px) {
                display: flex;
            }
            @include max(991px) {
                display: block;
                margin-bottom: 20px;
            }
            p{
                padding-bottom: 0;
                border-bottom: none;
            }
            span{
                font-style: normal;
            }
        }
        p{
            padding-bottom: 50px;
            border-bottom: 1px solid rgba(10, 16, 47, 0.1);
            @include max(1199px) {
                padding-bottom: 25px;
            }
        }
        .masco--contact-form-img {
            margin: 110px 0 30px 0;
            @include max(1399px) {
                margin: 50px 5px 25px 0;
            }
            @include max(1199px) {
                margin: 25px 5px 25px 0;
            }
        }
        span{
            font-style: italic;
            font-weight: 600;
        }
    }
}




