// portfolio 1
.masco--portfolio-section {
    position: relative;
    .masco--portfolio-bg-img {
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
    }
}
.masco--single-portfolio {
    @include max(767px) {
        margin-bottom: 30px;
    }
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 10px;
        margin-bottom: 24px;
    }
    .masco--single-portfolio-text {
        display: flex;
        justify-content: space-between;
        margin-right: 16px;
        @include max(1399px) {
            margin-right: 0;
        }
        a{
            p{
                font-weight: 600;
                font-size: 24px;
                line-height: 32px;
                letter-spacing: -0.5px;
                color: var(--gray-900);
            }
        }
        a{
            span{
                font-family: $body-font;
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                color: var(--gray-900);
            }
        }
    }
    .read-more-btn {
        margin-top: 30px;
    }
}
.masco--gallery-wrap {
    margin-left: -60px;
}
.masco--single-portfolio {
    overflow: hidden;
    margin-left: 60px;
}

// portfolio 2
.masco--portfolio2-section {
    .masco--portfolio2-img {
        position: relative;
    }
    .masco--portfolio2-playbtn {
            position: absolute;
            top: 50%;
            right: 50%;
            transform: translate(50%,-50%);
            background-color: var(--warning-700);
            width: 100px;
            height: 100px;
            text-align: center;
            line-height: 100px;
            border-radius: 50%;
    }
}


// portfolio classic
.masco--portfolio-classic-section {
    .masco--single-portfolio {
        margin-bottom: 50px;
    }
}
.portfolio-menu {
    margin-bottom: 80px;
    ul{
        display: flex;
        justify-content: center;
        .active {
            background-color: var(--primary-700);
            color: var(--warning-700);
        }
        li{
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: rgba(10, 16, 47, 0.6);
            background: rgba(10, 16, 47, 0.04);
            border-radius: 50px;
            padding: 13px 42px;
            margin-right: 24px;
            cursor: pointer;
            display: inline-block;
        }
    }
}
.portfolio-btn {
    text-align: center;
    margin-top: 30px
}


// portfolio masonry
.masco--portfolio-masonry {
    .masco--single-portfolio {
        position: relative;
        margin-bottom: 24px;
        img{
            margin: 0;
        }
        &:hover .masco--single-portfolio-text {
            opacity: 1;
            transition: all 0.4s ease;
        }
        .masco--single-portfolio-text {
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            bottom: 0;
            left: 0;
            margin-right: 0;
            opacity: 0;
            a{
                p{
                    color: var(--warning-700);
                    margin-bottom: 10px;
                }
                span{
                    color: var(--warning-700);
                }
            }
        }
    }
}


// portfolio modern
.masco--portfolio-modern {
    .masco--single-portfolio-modern {
        margin-bottom: 40px;
        img{
            object-fit: cover;
            object-position: center center;
            border-radius: 15px;
            margin-bottom: 34px;
            transition: all 0.4s ease;
            &:hover {
                box-shadow: 10px 10px var(--gray-900);
            }
        }
        .portfolio-modern-content {
            display: flex;
            .portfolio-modern-catagory {
                h4{
                    font-weight: 400;
                }
            }
        }
    }
}


// portfolio details
.masco--portfolio-details {
    max-width: 1076px;
    margin: 0 auto;
    ol{
        list-style: decimal !important;
    }
    .portfolio-details-img {
        margin-bottom: 30px;
        img{
            object-fit: cover;
            object-position: center center;
            width: 100%;
            border-radius: 10px;
        }
    }
    .all-portfolio-details-content {
        display: flex;
        justify-content: space-between;
        margin-bottom: 60px;
        .portfolio-details-content {
            a{
                color: rgba(10, 16, 47, 0.8);
            }
        }
    }
}