// home 1
.masco--hero-section{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include max(1399px) {
        padding-top: 100px;
    }
    .masco--hero-content{
        h1{
            @include max(1199px) {
                margin-bottom: 10px;
            }
            @include max(991px) {
                margin: 0 100px 20px;
                text-align: center;
            }
            @include max(767px) {
                margin: 0 70px 20px;
            }
        }
        p{
            margin-right: 30px;
            margin-bottom: 50px;
            @include max(991px) {
                text-align: center;
            }
        }
    }
    .masco--hero-video-img{
        position: relative;
        object-fit: cover;
        object-position: center center;
        text-align: right;
        @include max(991px) {
            text-align: center;
        }
        img{
            border-radius: 10px;
        }
        .masco--hero-playbtn{
            position: absolute;
            top: 50%;
            right: 31%;
            transform: translate(-50%,-50%);
            background-color: var(--warning-700);
            width: 100px;
            height: 100px;
            text-align: center;
            line-height: 100px;
            border-radius: 50%;
            @include max(1399px) {
                right: 48%;
            }
            img{
                border-radius: 0;
            }
        }
    }
}
.masco--subscription-box{
    margin-bottom: 20px;
    margin-right: 200px;
    position: relative;
    @include max(1399px) {
        margin-right: 160px;
    }
    @include max(1199px) {
        margin-right: 80px;
        margin-bottom: 10px;
    }
    @include max(991px) {
        margin: 20px 30px;
    }
    input[type=email] {
        border: 1px solid var(--gray-900) !important;
        border-radius: 50px;
        background-color: var(--warning-700);
    }
    #masco-subscribe-btn {
        color: var(--warning-700);
        background-color: var(--primary-700);
        padding: 13px 30px;
        border-radius: 50px;
        position: absolute;
        top: 5px;
        right: 5px;
        font-family: $body-font;
        font-size: 16px;
        line-height: 24px;
        font-weight: 700;
        transition: all 0.4s ease;
        &:hover {
            background-color: var(--gray-900);
        }
    }
}


// home 2
.masco--chat-software {
    margin-top: 15px;
}
.home2{
    @include max(991px) {
        padding-top: 50px;
    }
    .masco--hero-content {
        h1{
            @include max(991px) {
                margin: 0 45px 20px;
            }
        }
        p{
            margin-bottom: 50px;
        }
    }
    .masco--hero-section-buttons {
        margin-bottom: 24px;
        @include max(991px) {
            text-align: center;
        }
        .masco--learnmore-btn {
            margin-left: 20px;
            padding: 18px 30px;
             box-sizing: border-box;
        }
    }
    .masco--hero-img {
        text-align: right;
        margin-top: -22px;
        @include max(991px) {
            text-align: center;
        }
    }
}


// home 3
.masco--fitness-app {
    .masco--header-area {
        position: absolute;
        padding-top: 15px;
    }
}
.home3{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 180px;
    .masco--hero-content {
        .masco--cutomers-rating{
            display: flex;
            .masco--home3-hero-customers {
                display: flex;
                justify-content: space-between;
                a{
                    margin-top: 10px;
                }
                .masco--customer-content{
                    margin-left: 10px;
                    h3{
                        font-family: $body-font;
                        letter-spacing: -1px;
                    }
                    p{
                        font-family: $body-font;
                        font-weight: 400;
                        font-size: 16px;
                        color: var(--gray-900);
                    }
                }
            }
            .masco--home3-hero-rating {
                padding-left: 23px;
                &::before{
                    content: '';
                    width: 2px;
                    height: 39px;
                    background-color: var(--gray-900);
                }
            }
        }
    }
    .masco--hero-img {
        text-align: center;
        position: relative;
        z-index: 1;
        .masco--home3-hero-shape1 {
            position: absolute;
            bottom: 17%;
            left: 12.5%;
            z-index: -1;
        }
        .masco--home3-hero-shape2 {
            position: absolute;
            top: 15%;
            right: -7%;
            z-index: -1;
        }
    }
}