// iconbox 1
.iconbox {
    display: flex;
    border: 1px solid var(--warning-800);
    border-radius: 10px;
    padding: 40px;
    margin-bottom: 24px;
    &:hover {
        background-color: var(--warning-900);
        transition: all 0.4s ease;
        border: 1px solid var(--warning-900);
    }
    .iconbox-img {
        flex-shrink: 0;
        margin-right: 40px;
    }
}


// iconbox 2
.iconbox-2 {
    border: 2px solid var(--gray-900);
    border-radius: 10px;
    padding: 30px;
    &:hover{
        box-shadow: 8px 8px;
        transition: all 0.4s ease;
    }
    @include max(991px) {
        margin-bottom: 20px;
    }
    h4{
        margin-top: 30px;
        letter-spacing: -0.5px;
    }
}