// testimonial 1
.masco--testimonial-area {
    background-color: var(--warning-900);
    .masco--testimonial-img {
        @include max(991px) {
            text-align: center;
            margin-bottom: 50px;
        }
        img{
            object-fit: cover;
            object-position: center top;
        }
    }
    .masco--testimonial-content {
        margin-left: 79px;
        @include max(1399px) {
            margin-left: 20px;
        }
        @include max(1199px) {
            margin-left: 10px;
        }
        @include max(767px) {
            margin-bottom: 20px;
        }
        img{
            object-fit: cover;
            object-position: center top;
        }
        p{
            font-weight: 600;
            font-size: 24px;
            line-height: 32px;
            letter-spacing: -0.5px;
            color: var(--gray-900);
            margin: 30px 0;
        }
        .testimonial-identity {
            margin-bottom: 50px;
            @include max(991px) {
                margin-bottom: 20px;
            }
            p{
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: var(--gray-900);
                margin: 0;
            }
            span{
                font-weight: 600;
                font-size: 20px;
                line-height: 28px;
                color: var(--gray-900);
                opacity: 0.8;
            }
        }
    }
}


// testimonial 2
.masco--testimonial2-section {
    background-color: rgba(162, 89, 255, 0.03);
    .masco--single-testimonial2 {
        text-align: center;
        @include max(991px) {
            margin-bottom: 30px;
        }
        p{
            color: var(--gray-900);
            margin-top: 30px;
            opacity: 1;
        }
    }
    .testimonial2-btn {
        margin-top: 80px;
        text-align: center;
        @include max(991px) {
            margin-top: 50px;
        }
    }
    
}


// testimonial 3
.masco--testimonial3-section {
    background-color: var(--warning-900);
    .masco--testimonial3-title{
        margin-bottom: 80px;
        @include max(767px) {
            margin: 40px 0;
            text-align: center;
        }
    }
    .masco--testimonial3-btn {
        margin-top: 40px;
        @include max(767px) {
            margin-top: 0;
            margin-bottom: 20px;
        }
    }
    .rt {
        @include max(767px) {
            text-align: center;
        }
    }
    .masco--single-testimonial3 {
        background-color: var(--warning-700);
        border-radius: 10px;
        padding: 30px;
        margin-bottom: 24px;
        .masco--single-content {
            @include max(1199px) {
                display: flex;
            }
        }
        .masco--single-testimonial3-images {
            margin-bottom: 30px;
        }
        span{
            font-weight: 600;
            color: var(--gray-900);
        }
        p{
            margin-top: 5px;
            margin-bottom: 24px;
            color: var(--gray-900);
        }
    }
}