// team 1
.masco--team-section {
    background-color: var(--warning-900);
    .masco--single-team {
        text-align: center;
        padding: 20px;
        background-color: var(--warning-700);
        border-radius: 10px;
        @include max(991px) {
            margin-bottom: 24px;
        }
        @include max(767px) {
            margin: 20px 100px;
        }
        h5{
            margin-top: 24px;
            margin-bottom: 5px;
        }
        p{
            margin-bottom: 15px;
        }
        .masco--team-social-icon {
            ul{
                display: flex;
                justify-content: center;
                li{
                    width: 35px;
                    height: 35px;
                    margin-right: 15px;
                    line-height: 35px;
                    a{
                        display: block;
                        background-color: rgba(10, 16, 47, 0.05);
                        text-align: center;
                        border-radius: 50%;
                        &:hover {
                            background-color: var(--gray-900);
                            path{
                                fill: var(--warning-700);
                            }
                        }
                    }
                }
            }
        }
    }
}

// team 2
.masco--team-section2 {
    background-color: var(--warning-700);
    @include max(991px) {
        margin-top: 40px;
    }
    .masco--single-team2{
        background-color: var(--warning-900);
        &:hover {
            background-color: var(--warning-700);
            box-shadow: 0px 4px 80px rgba(0, 0, 0, 0.08);
            transition: all 0.4s ease;
        }
    }
    .team-title {
        margin-top: 100px;
    }
    .masco--single-team3 {
        background-color: var(--warning-900);
        margin-bottom: 24px;
    }
    .team-btn {
        margin-top: 56px;
        text-align: center;
    }
}
