.masco--icon-list{
    @include max(991px) {
        margin: 0 150px;
    }
    @include max(991px) {
        margin: 0 60px;
    }
    @include max(767px) {
        margin: 0;
    }
    li{
        font-size: 14px;
        line-height: 20px;
        position: relative;
        margin-left: 25px;
        img{
            position: absolute;
            top: -15%;
            left: -5%;
        }
    }
}

// icon list 2
.masco--icon-list2 {
    li{
        color: var(--gray-900);
        font-weight: 600;
        position: relative;
        margin-bottom: 20px;
        margin-left: 35px;
        text-align: start;
        img{
            position: absolute;
            top: 5%;
            left: -6%;
        }
    }
}


