h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $heading-font;
  color: var(--gray-900);
  font-weight: 800;
}
h1,
.h1 {
  font-size: 90px;
  line-height: 1.11em;
  margin-bottom: 20px;
  letter-spacing: -1px;
  @include max(1400px) { 
    font-size: 70px;
  }
  @include max(1199px) { 
    font-size: 60px;
  }
  @include max(991px) { 
    font-size: 48px;
  }
  @include max(767px) { 
    font-size: 36px;
  }
}
h2,
.h2 {
  font-size: 46px;
  line-height: 1.26em;
  margin-bottom: 15px;
  @include max(991px) { 
    font-size: 40px;
    line-height: 50px;
  }
  @include max(767px) { 
    font-size: 32px;
    line-height: 40px;
  }
}

h3,
.h3 {
  font-size: 30px;
  line-height: 1.4em;
  margin-bottom: 15px;
  font-weight: 700;
  @include max(767px) { 
    font-size: 24px;
    line-height: 34px;
  }
}
h4,
.h4 {
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 15px;
  font-weight: 600;
}
h5,
.h5 {
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
  margin-bottom: 15px;
}

p{
  margin-bottom: 30px;
  @include max(767px) {
    margin-bottom: 20px;
  }
  &:last-child{
    margin-bottom: 0;
  }
}

