@for $i from 10 through 40 {
  .f-size-#{$i} {
    font-size: $i * 1px !important;
  }
}

@for $i from 10 through 40 {
  .line-height-#{$i} {
    line-height: $i * 1px !important;
  }
}

.font-bold{
  font-weight: 700;
}
.font-black{
  font-weight: 800;
}
.font-medium{
  font-weight: 500;
}
.font-normal{
  font-weight: 400;
}
.font-semibold{
  font-weight: 600;
}

// padding
.pd{
  padding: 120px 0;
  @include max(1199px) {
    padding: 100px 0;
  }
  @include max(991px) {
      padding: 80px 0;
  }
  @include max(767px) {
    padding: 60px 0;
}
}

.pdt{
  padding-top: 120px;
  @include max(1199px) {
    padding-top: 100px;
  }
  @include max(991px) {
      padding-top: 80px;
  }
  @include max(767px) {
    padding-top: 60px;
}
}

.bg-color{
  background-color: var(--warning-900) !important;
}

.text-center{
  text-align: center;
}
.text-rt{
  text-align: right;
}

.responsive_mb{
  @include max(991px) {
    margin-bottom: 30px;
  }
}
.opacity-2{
  opacity: 0.2 !important;
}
.mb-60{
  margin-bottom: 60px !important;
}
.mb-15{
  margin-bottom: 15px;
}

