// readmore button style
.read-more-btn {
  display: inline-block;
  path{
    stroke: var(--gray-900);
  }
  &:hover path{
    stroke: var(--primary-700);
  }
  color: var(--gray-900);
  span{
    margin-right: 10px;
  }
  &:hover{
    color: var(--primary-700);
  }
}


// button style
.masco-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  border-radius: $buton_border_radius;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  width: fit-content;
  position: relative;
  z-index: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--warning-700);
  background-color: var(--primary-700);

  &.masco-header1-btn{
    padding: 13px 33px;
  }
  &.masco-header2-btn{
    padding: 13px 33px;
    color: var(--warning-700);
    background-color: var(--gray-900);
    &:hover {
      color: var(--warning-700);
      background-color: var(--primary-700);
    }
  }
  &.pink-btn{
    background-color: var(--pink-400);
  }
  &.white-btn{
    border: 2px solid var(--gray-900);
    color: var(--gray-900);
    background-color: var(--warning-700);
    &:hover {
      background-color: var(--pink-400);
      border: 2px solid var(--pink-400);
    }
  }
  &.black-btn{
    background-color: var(--gray-900);
    &:hover {
      background-color: var(--primary-700);
    }
  }

  &.yellow-btn{
    color: var(--gray-900);
    background-color: var(--warning-550);
    border: 2px solid var(--gray-900);
    &:hover {
      color: var(--gray-900);
      background-color: var(--warning-550);
      box-shadow: 6px 6px var(--warning-550);
    }
  }

  &.white-blue-btn{
    color: var(--gray-900);
    background-color: var(--warning-700);
    border: 2px solid var(--gray-900);
    &:hover{
      color: var(--warning-700);
      background-color: var(--primary-700);
      border: 2px solid var(--primary-700);
    }
  }


  &:hover {
    color: var(--warning-700);
    background-color: var(--gray-900);
  }

  &.disabled,
  &:disabled {
    opacity: 1;
  }
  &.d-block {
    width: 100%;
  }
  &:focus {
    box-shadow: none;
  }
  &.pill {
    border-radius: 999px;
  }
}







